<template>
	<div class="addEdit-block">
		<el-form
			class="add-update-preview"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="180px"
		>
			<template >
				<el-form-item class="input" v-if="type!='info'"  label="" prop="kechengmingcheng" >
					<el-input v-model="ruleForm.kechengmingcheng" placeholder="" clearable  :readonly="ro.kechengmingcheng"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="kechengmingcheng" >
					<el-input v-model="ruleForm.kechengmingcheng" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="upload" v-if="type!='info' && !ro.fengmian" label="" prop="fengmian" >
					<file-upload
						tip=""
						action="file/upload"
						:limit="3"
						:multiple="true"
						:fileUrls="ruleForm.fengmian?ruleForm.fengmian:''"
						@change="fengmianUploadChange"
					></file-upload>
				</el-form-item>
				<el-form-item class="upload" v-else-if="ruleForm.fengmian" label="" prop="fengmian" >
					<img v-if="ruleForm.fengmian.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.fengmian.split(',')[0]" width="100" height="100">
					<img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.fengmian.split(',')" :src="$base.url+item" width="100" height="100">
				</el-form-item>
				<el-form-item class="select" v-if="type!='info'"  label="" prop="xuekefenlei" >
					<el-select :disabled="ro.xuekefenlei" v-model="ruleForm.xuekefenlei" placeholder="" >
						<el-option
							v-for="(item,index) in xuekefenleiOptions"
							v-bind:key="index"
							:label="item"
							:value="item">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="xuekefenlei" >
					<el-input v-model="ruleForm.xuekefenlei"
						placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="jiaoshounianji" >
					<el-input v-model="ruleForm.jiaoshounianji" placeholder="" clearable  :readonly="ro.jiaoshounianji"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="jiaoshounianji" >
					<el-input v-model="ruleForm.jiaoshounianji" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="xiaoshijiage" >
					<el-input v-model.number="ruleForm.xiaoshijiage" placeholder="" clearable  :readonly="ro.xiaoshijiage"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="xiaoshijiage" >
					<el-input v-model="ruleForm.xiaoshijiage" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="keyueshijian" >
					<el-input v-model="ruleForm.keyueshijian" placeholder="" clearable  :readonly="ro.keyueshijian"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="keyueshijian" >
					<el-input v-model="ruleForm.keyueshijian" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="select" v-if="type!='info'" label="" prop="jiajiaozhanghao" >
					<el-select :disabled="ro.jiajiaozhanghao" @change="jiajiaozhanghaoChange" v-model="ruleForm.jiajiaozhanghao" placeholder="">
						<el-option
							v-for="(item,index) in jiajiaozhanghaoOptions"
							v-bind:key="index"
							:label="item"
							:value="item">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item class="input" v-else-if="ruleForm.jiajiaozhanghao" label="" prop="jiajiaozhanghao" >
					<el-input v-model="ruleForm.jiajiaozhanghao" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="jiajiaoxingming" >
					<el-input v-model="ruleForm.jiajiaoxingming" placeholder="" clearable  :readonly="ro.jiajiaoxingming"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="jiajiaoxingming" >
					<el-input v-model="ruleForm.jiajiaoxingming" placeholder="" readonly></el-input>
				</el-form-item>
			</template>
			<el-form-item class="btn">
				<el-button class="btn3"  v-if="type!='info'" type="success" @click="onSubmit">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
				<el-button class="btn4" v-if="type!='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
				<el-button class="btn5" v-if="type=='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
			</el-form-item>
		</el-form>
    

	</div>
</template>
<script>
	import { 
		isIntNumer,
	} from "@/utils/validate";
	export default {
		data() {
			var validateIntNumber = (rule, value, callback) => {
				if(!value){
					callback();
				} else if (!isIntNumer(value)) {
					callback(new Error(""));
				} else {
					callback();
				}
			};
			return {
				id: '',
				type: '',
			
			
				ro:{
					kechengmingcheng : false,
					fengmian : false,
					xuekefenlei : false,
					jiaoshounianji : false,
					xiaoshijiage : false,
					keyueshijian : false,
					jiajiaozhanghao : false,
					jiajiaoxingming : false,
					clicktime : false,
					discussnum : false,
				},
			
				ruleForm: {
					kechengmingcheng: '',
					fengmian: '',
					xuekefenlei: '',
					jiaoshounianji: '',
					xiaoshijiage: '',
					keyueshijian: '',
					jiajiaozhanghao: '',
					jiajiaoxingming: '',
					clicktime: '',
				},
		
				xuekefenleiOptions: [],
				jiajiaozhanghaoOptions: [],

				rules: {
					kechengmingcheng: [
						{ required: true, message: '', trigger: 'blur' },
					],
					fengmian: [
					],
					xuekefenlei: [
						{ required: true, message: '', trigger: 'blur' },
					],
					jiaoshounianji: [
					],
					xiaoshijiage: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					keyueshijian: [
					],
					jiajiaozhanghao: [
					],
					jiajiaoxingming: [
					],
					clicktime: [
					],
					discussnum: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
				},
			};
		},
		props: ["parent"],
		computed: {



		},
		components: {
		},
		created() {
		},
		methods: {
			// 
			download(file){
				window.open(`${file}`)
			},
			// 
			init(id,type) {
				if (id) {
					this.id = id;
					this.type = type;
				}
				if(this.type=='info'||this.type=='else'){
					this.info(id);
				}else if(this.type=='logistics'){
					this.logistics=false;
					this.info(id);
				}else if(this.type=='cross'){
					var obj = this.$storage.getObj('crossObj');
					for (var o in obj){
						if(o=='kechengmingcheng'){
							this.ruleForm.kechengmingcheng = obj[o];
							this.ro.kechengmingcheng = true;
							continue;
						}
						if(o=='fengmian'){
							this.ruleForm.fengmian = obj[o];
							this.ro.fengmian = true;
							continue;
						}
						if(o=='xuekefenlei'){
							this.ruleForm.xuekefenlei = obj[o];
							this.ro.xuekefenlei = true;
							continue;
						}
						if(o=='jiaoshounianji'){
							this.ruleForm.jiaoshounianji = obj[o];
							this.ro.jiaoshounianji = true;
							continue;
						}
						if(o=='xiaoshijiage'){
							this.ruleForm.xiaoshijiage = obj[o];
							this.ro.xiaoshijiage = true;
							continue;
						}
						if(o=='keyueshijian'){
							this.ruleForm.keyueshijian = obj[o];
							this.ro.keyueshijian = true;
							continue;
						}
						if(o=='jiajiaozhanghao'){
							this.ruleForm.jiajiaozhanghao = obj[o];
							this.ro.jiajiaozhanghao = true;
							continue;
						}
						if(o=='jiajiaoxingming'){
							this.ruleForm.jiajiaoxingming = obj[o];
							this.ro.jiajiaoxingming = true;
							continue;
						}
						if(o=='clicktime'){
							this.ruleForm.clicktime = obj[o];
							this.ro.clicktime = true;
							continue;
						}
						if(o=='discussnum'){
							this.ruleForm.discussnum = obj[o];
							this.ro.discussnum = true;
							continue;
						}
					}
				}
				// 
				this.$http({
					url: `${this.$storage.get('sessionTable')}/session`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						var json = data.data;
					} else {
						this.$message.error(data.msg);
					}
				});
				this.$http({
					url: `option/xuekefenlei/xuekefenlei`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						this.xuekefenleiOptions = data.data;
					} else {
						this.$message.error(data.msg);
					}
				});
				this.$http({
					url: `option/jiajiao/jiajiaozhanghao`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						this.jiajiaozhanghaoOptions = data.data;
					} else {
						this.$message.error(data.msg);
					}
				});
			
			},
			// 
			jiajiaozhanghaoChange () {
				this.$http({
					url: `follow/jiajiao/jiajiaozhanghao?columnValue=`+ this.ruleForm.jiajiaozhanghao,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						if(data.data.jiajiaoxingming){
							this.ruleForm.jiajiaoxingming = data.data.jiajiaoxingming
						}
					} else {
						this.$message.error(data.msg);
					}
				});
			},
			// 

			info(id) {
				this.$http({
					url: `jiajiaoxinxi/info/${id}`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						this.ruleForm = data.data;
						//
						let reg=new RegExp('../../../upload','g')//g
					} else {
						this.$message.error(data.msg);
					}
				});
			},

			// 
			async onSubmit() {
					if(this.ruleForm.fengmian!=null) {
						this.ruleForm.fengmian = this.ruleForm.fengmian.replace(new RegExp(this.$base.url,"g"),"");
					}
var objcross = this.$storage.getObj('crossObj');
					await this.$refs["ruleForm"].validate(async valid => {
						if (valid) {
							if(this.type=='cross'){
								var statusColumnName = this.$storage.get('statusColumnName');
								var statusColumnValue = this.$storage.get('statusColumnValue');
								if(statusColumnName!='') {
									var obj = this.$storage.getObj('crossObj');
									if(statusColumnName && !statusColumnName.startsWith("[")) {
										for (var o in obj){
											if(o==statusColumnName){
												obj[o] = statusColumnValue;
											}
										}
										var table = this.$storage.get('crossTable');
										await this.$http({
											url: `${table}/update`,
											method: "post",
											data: obj
										}).then(({ data }) => {});
									}
								}
							}
							
							await this.$http({
								url: `jiajiaoxinxi/${!this.ruleForm.id ? "save" : "update"}`,
								method: "post",
								data: this.ruleForm
							}).then(async ({ data }) => {
								if (data && data.code === 0) {
									this.$message({
										message: "",
										type: "success",
										duration: 1500,
										onClose: () => {
											this.parent.showFlag = true;
											this.parent.addOrUpdateFlag = false;
											this.parent.jiajiaoxinxiCrossAddOrUpdateFlag = false;
											this.parent.search();
											this.parent.contentStyleChange();
										}
									});
								} else {
									this.$message.error(data.msg);
								}
							});
						}
					});
			},
			// uuid
			getUUID () {
				return new Date().getTime();
			},
			// 
			back() {
				this.parent.showFlag = true;
				this.parent.addOrUpdateFlag = false;
				this.parent.jiajiaoxinxiCrossAddOrUpdateFlag = false;
				this.parent.contentStyleChange();
			},
			fengmianUploadChange(fileUrls) {
				this.ruleForm.fengmian = fileUrls;
			},
		}
	};
</script>
