					<el-input v-model="ruleForm.xuehao" readonly						placeholder="" clearable></el-input>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","width":"49%","padding":"0","margin":"0 0 26px 0","display":"inline-block"}'   v-if="flag=='xuesheng'"  label="" prop="xueshengxingming">
					<el-input v-model="ruleForm.xueshengxingming" 						placeholder="" clearable></el-input>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","width":"49%","padding":"0","margin":"0 0 26px 0","display":"inline-block"}' v-if="flag=='xuesheng'" label="" prop="touxiang">
					<file-upload
						tip=""
						action="file/upload"
						:limit="3"
						:multiple="true"
						:fileUrls="ruleForm.touxiang?ruleForm.touxiang:''"
						@change="xueshengtouxiangUploadChange"
					></file-upload>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","width":"49%","padding":"0","margin":"0 0 26px 0","display":"inline-block"}' v-if="flag=='xuesheng'"  label="" prop="xingbie">
					<el-select v-model="ruleForm.xingbie"  placeholder="">
						<el-option
							v-for="(item,index) in xueshengxingbieOptions"
							v-bind:key="index"
							:label="item"
							:value="item">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","width":"49%","padding":"0","margin":"0 0 26px 0","display":"inline-block"}'   v-if="flag=='xuesheng'"  label="" prop="shoujihaoma">
					<el-input v-model="ruleForm.shoujihaoma" 						placeholder="" clearable></el-input>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","width":"49%","padding":"0","margin":"0 0 26px 0","display":"inline-block"}' v-if="flag=='xuesheng'" label="" prop="jiazhangzhanghao">
					<el-select  @change="xueshengjiazhangzhanghaoChange"  v-model="ruleForm.jiazhangzhanghao" placeholder="">
						<el-option
							v-for="(item,index) in xueshengjiazhangzhanghaoOptions"
							v-bind:key="index"
							:label="item"
							:value="item">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","width":"49%","padding":"0","margin":"0 0 26px 0","display":"inline-block"}'   v-if="flag=='xuesheng'"  label="" prop="jiazhangxingming">
					<el-input v-model="ruleForm.jiazhangxingming" 						placeholder="" clearable></el-input>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","width":"49%","padding":"0","margin":"0 0 26px 0","display":"inline-block"}'   v-if="flag=='jiazhang'"  label="" prop="jiazhangzhanghao">
					<el-input v-model="ruleForm.jiazhangzhanghao" readonly						placeholder="" clearable></el-input>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","width":"49%","padding":"0","margin":"0 0 26px 0","display":"inline-block"}'   v-if="flag=='jiazhang'"  label="" prop="jiazhangxingming">
					<el-input v-model="ruleForm.jiazhangxingming" 						placeholder="" clearable></el-input>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","width":"49%","padding":"0","margin":"0 0 26px 0","display":"inline-block"}' v-if="flag=='jiazhang'" label="" prop="touxiang">
					<file-upload
						tip=""
						action="file/upload"
						:limit="3"
						:multiple="true"
						:fileUrls="ruleForm.touxiang?ruleForm.touxiang:''"
						@change="jiazhangtouxiangUploadChange"
					></file-upload>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","width":"49%","padding":"0","margin":"0 0 26px 0","display":"inline-block"}' v-if="flag=='jiazhang'"  label="" prop="xingbie">
					<el-select v-model="ruleForm.xingbie"  placeholder="">
						<el-option
							v-for="(item,index) in jiazhangxingbieOptions"
							v-bind:key="index"
							:label="item"
							:value="item">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","width":"49%","padding":"0","margin":"0 0 26px 0","display":"inline-block"}'   v-if="flag=='jiazhang'"  label="" prop="lianxidianhua">
					<el-input v-model="ruleForm.lianxidianhua" 						placeholder="" clearable></el-input>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","width":"49%","padding":"0","margin":"0 0 26px 0","display":"inline-block"}'   v-if="flag=='jiajiao'"  label="" prop="jiajiaozhanghao">
					<el-input v-model="ruleForm.jiajiaozhanghao" readonly						placeholder="" clearable></el-input>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","width":"49%","padding":"0","margin":"0 0 26px 0","display":"inline-block"}' v-if="flag=='jiajiao'" label="" prop="touxiang">
					<file-upload
						tip=""
						action="file/upload"
						:limit="3"
						:multiple="true"
						:fileUrls="ruleForm.touxiang?ruleForm.touxiang:''"
						@change="jiajiaotouxiangUploadChange"
					></file-upload>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","width":"49%","padding":"0","margin":"0 0 26px 0","display":"inline-block"}'   v-if="flag=='jiajiao'"  label="" prop="jiajiaoxingming">
					<el-input v-model="ruleForm.jiajiaoxingming" 						placeholder="" clearable></el-input>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","width":"49%","padding":"0","margin":"0 0 26px 0","display":"inline-block"}' v-if="flag=='jiajiao'"  label="" prop="xingbie">
					<el-select v-model="ruleForm.xingbie"  placeholder="">
						<el-option
							v-for="(item,index) in jiajiaoxingbieOptions"
							v-bind:key="index"
							:label="item"
							:value="item">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","width":"49%","padding":"0","margin":"0 0 26px 0","display":"inline-block"}'   v-if="flag=='jiajiao'"  label="" prop="lianxidianhua">
					<el-input v-model="ruleForm.lianxidianhua" 						placeholder="" clearable></el-input>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","width":"49%","padding":"0","margin":"0 0 26px 0","display":"inline-block"}' v-if="flag=='jiajiao'" label="" prop="jiajiaozizhi">
					<file-upload
						tip=""
						action="file/upload"
						:limit="3"
						:multiple="true"
						:fileUrls="ruleForm.jiajiaozizhi?ruleForm.jiajiaozizhi:''"
						@change="jiajiaojiajiaozizhiUploadChange"
					></file-upload>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","width":"49%","padding":"0","margin":"0 0 26px 0","display":"inline-block"}' v-if="flag=='users'" label="" prop="username">
					<el-input v-model="ruleForm.username" placeholder=""></el-input>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","width":"49%","padding":"0","margin":"0 0 26px 0","display":"inline-block"}' v-if="flag=='users'" label="" prop="image">
					<file-upload
						tip=""
						action="file/upload"
						:limit="1"
						:multiple="false"
						:fileUrls="ruleForm.image?ruleForm.image:''"
						@change="usersimageUploadChange"
					></file-upload>
				</el-form-item>
				<el-form-item :style='{"padding":"0","margin":"20px 0 0"}'>
					<el-button class="btn3" :style='{"border":"0px solid #ccc","cursor":"pointer","padding":"0 10px","margin":"0 10px 0 0","color":"#fff","borderRadius":"6px","background":"#6ea0dc","width":"auto","fontSize":"16px","minWidth":"110px","height":"40px"}' type="primary" @click="onUpdateHandler">
						<span class="icon iconfont icon-xihuan" :style='{"margin":"0 2px","fontSize":"14px","color":"#fff","display":"none","height":"40px"}'></span>
						
					</el-button>
				</el-form-item>
			</el-row>
		</el-form>
	</div>
</template>
<script>
// 
import { 
	isMobile,
} from "@/utils/validate";

export default {
	data() {
		return {
			ruleForm: {},
			flag: '',
			usersFlag: false,
			xueshengxingbieOptions: [],
			xueshengjiazhangzhanghaoOptions: [],
			jiazhangxingbieOptions: [],
			jiajiaoxingbieOptions: [],
		};
	},
	mounted() {
		var table = this.$storage.get("sessionTable");
		this.flag = table;
		this.$http({
			url: `${this.$storage.get("sessionTable")}/session`,
			method: "get"
		}).then(({ data }) => {
			if (data && data.code === 0) {
				this.ruleForm = data.data;
			} else {
				this.$message.error(data.msg);
			}
		});
		this.xueshengxingbieOptions = ",".split(',')
		this.$http({
			url: `option/jiazhang/jiazhangzhanghao`,
			method: "get"
		}).then(({ data }) => {
			if (data && data.code === 0) {
				this.xueshengjiazhangzhanghaoOptions = data.data;
			} else {
				this.$message.error(data.msg);
			}
		});
		this.jiazhangxingbieOptions = ",".split(',')
		this.jiajiaoxingbieOptions = ",".split(',')
	},
	methods: {
		// 
		xueshengjiazhangzhanghaoChange () {
			this.$http({
				url: `follow/jiazhang/jiazhangzhanghao?columnValue=`+ this.ruleForm.jiazhangzhanghao,
				method: "get"
			}).then(({ data }) => {
				if (data && data.code === 0) {
					if(data.data.jiazhangxingming){
						this.ruleForm.jiazhangxingming = data.data.jiazhangxingming
					}
				} else {
					this.$message.error(data.msg);
				}
			});
		},
		xueshengtouxiangUploadChange(fileUrls) {
			this.ruleForm.touxiang = fileUrls;
		},
		jiazhangtouxiangUploadChange(fileUrls) {
			this.ruleForm.touxiang = fileUrls;
		},
		jiajiaotouxiangUploadChange(fileUrls) {
			this.ruleForm.touxiang = fileUrls;
		},
		jiajiaojiajiaozizhiUploadChange(fileUrls) {
			this.ruleForm.jiajiaozizhi = fileUrls;
		},
		usersimageUploadChange(fileUrls) {
			this.ruleForm.image = fileUrls;
		},
		onUpdateHandler() {
			if((!this.ruleForm.xuehao)&& 'xuesheng'==this.flag){
				this.$message.error('');
				return
			}


			if((!this.ruleForm.mima)&& 'xuesheng'==this.flag){
				this.$message.error('');
				return
			}

			if((!this.ruleForm.xueshengxingming)&& 'xuesheng'==this.flag){
				this.$message.error('');
				return
			}




			if(this.ruleForm.touxiang!=null) {
				this.ruleForm.touxiang = this.ruleForm.touxiang.replace(new RegExp(this.$base.url,"g"),"");
			}




			if( 'xuesheng' ==this.flag && this.ruleForm.shoujihaoma&&(!isMobile(this.ruleForm.shoujihaoma))){
				this.$message.error(``);
				return
			}




			if((!this.ruleForm.jiazhangzhanghao)&& 'jiazhang'==this.flag){
				this.$message.error('');
				return
			}


			if((!this.ruleForm.mima)&& 'jiazhang'==this.flag){
				this.$message.error('');
				return
			}

			if((!this.ruleForm.jiazhangxingming)&& 'jiazhang'==this.flag){
				this.$message.error('');
				return
			}




			if(this.ruleForm.touxiang!=null) {
				this.ruleForm.touxiang = this.ruleForm.touxiang.replace(new RegExp(this.$base.url,"g"),"");
			}




			if( 'jiazhang' ==this.flag && this.ruleForm.lianxidianhua&&(!isMobile(this.ruleForm.lianxidianhua))){
				this.$message.error(``);
				return
			}
			if((!this.ruleForm.jiajiaozhanghao)&& 'jiajiao'==this.flag){
				this.$message.error('');
				return
			}


			if((!this.ruleForm.mima)&& 'jiajiao'==this.flag){
				this.$message.error('');
				return
			}



			if(this.ruleForm.touxiang!=null) {
				this.ruleForm.touxiang = this.ruleForm.touxiang.replace(new RegExp(this.$base.url,"g"),"");
			}






			if( 'jiajiao' ==this.flag && this.ruleForm.lianxidianhua&&(!isMobile(this.ruleForm.lianxidianhua))){
				this.$message.error(``);
				return
			}
			if((!this.ruleForm.jiajiaozizhi)&& 'jiajiao'==this.flag){
				this.$message.error('');
				return
			}


			if(this.ruleForm.jiajiaozizhi!=null) {
				this.ruleForm.jiajiaozizhi = this.ruleForm.jiajiaozizhi.replace(new RegExp(this.$base.url,"g"),"");
			}




			if('users'==this.flag && this.ruleForm.username.trim().length<1) {
				this.$message.error(``);
				return	
			}
			if(this.flag=='users'){
				this.ruleForm.image = this.ruleForm.image.replace(new RegExp(this.$base.url,"g"),"")
			}
			this.$http({
				url: `${this.$storage.get("sessionTable")}/update`,
				method: "post",
				data: this.ruleForm
			}).then(({ data }) => {
				if (data && data.code === 0) {
					this.$message({
						message: "",
						type: "success",
						duration: 1500,
						onClose: () => {
							if(this.flag=='users'){
								this.$storage.set('headportrait',this.ruleForm.image)
							}
						}
					});
				} else {
					this.$message.error(data.msg);
				}
			});
		}
	}
};
</script>
