			}
	
	.add-update-preview .el-textarea /deep/ .el-textarea__inner {
				border: 1px solid #E8E8E8;
				border-radius: 0px;
				padding: 12px;
				color: #666;
				background: #fff;
				width: 100%;
				font-size: 15px;
				min-height: 150px;
				height: auto;
			}
	
	.add-update-preview .btn3 {
				border: 0px solid #ccc;
				cursor: pointer;
				border-radius: 6px;
				padding: 0 10px;
				margin: 0 10px 0 0;
				color: #fff;
				background: #6ea0dc;
